*{
 padding: 5px;
  margin: 0px;
}
.header{
    display:flex;
    justify-content: space-between;
    border: 1px solid black;
}
.logo{
    width:50px;
    padding:10px;
}
.nav-items ul {
    display:flex;
   
}
.nav-items ul li{
    font-size: 24px;
    padding:10px;
    margin:10px;
    list-style-type: none;
}
.res-cards{
    display: flex;
    flex-wrap: wrap; 
    justify-content: space-between;
   
   
}
.cards{
    width:200px;
    height:400px;
    /* border:1px solid black; */
    background-color: rgb(250, 233, 212);
   /* text-align: center; */
   padding:5px;
   margin-bottom: 10px;
   cursor: pointer;
   
 
  
}
.cards:hover{
    border:1px solid black;
}
.search{
    padding:10px;
}
.card-logo{
    width:98%;
    height:50%;
   
  
}
.filter-btn{
    margin-top:10px;
    margin-bottom: 10px;
    cursor: pointer;


}
.shimmer-container{
    display: flex;
}
.shimmer-card{
    margin:20px;
    width:200px;
    height:400px;
    background-color: gray;
}
.login{
    padding:0 20px;
    cursor: pointer;
    

}
.filter{
   display: flex;
}
.user-card{
    border: 2px solid black;
    margin-top: 20px;
}
.category{
 display: flex;
}
.button{
    margin: 5px;
    border:rgb(227, 217, 217) 1px solid;
    background: transparent;
    border-radius: 15%;
    cursor: pointer;
}
.button.veg {
  border: 2px solid rgb(53, 154, 53); 
}
.button.non-veg{
 border: 2px solid rgb(246, 42, 11);
}